<template >
  <div id="main" >
    <HotelList :searchresult="this.$cookies.get(searchresult)"></HotelList>
    <el-container class="reserve-container" style="background-color: #8492a6">
      <!--      <这是最顶部>-->
      <el-header class="first-header" style="position: relative;width: 100%;height: 10%;">
        <img src="../../static/logo.png" style="width: 20%;height: 10%;position: relative;left: 10%" >
        <div>
          <i class="el-icon-user-solid"  style="font-size: 30px;position: relative;right:70% ">
            <el-link :underline="false" type="primary" style="font-size: 20px;color: rgb(0,0,0)">我的用户</el-link>
          </i>
          <i class="el-icon-s-order" style="font-size: 30px;position: relative;right:40%">
            <el-link :underline="false" type="primary" style="font-size: 20px;color: rgb(0,0,0)">我的订单</el-link>
          </i>
        </div>
      </el-header>
      <el-main>
        <!--        <这是搜索部分>-->
        <el-header class="secend-header"  style="width: 50%; height: 30%; position: relative; left: 25%;">
          <div style="height: 100%;width: 100%;" >
            <img src="../../static/pexels-sindre-strøm-1144176.jpg" style="height: 100%;width: 100%;left: 0px; top: 0px; position: absolute; border-radius: 30px; box-shadow: rgba(0, 0, 0, 0.1) 0px 0px 0px 0px;" >
            <span style="font-size:28px;line-height:1.3;position: relative;left:-40%;color: #f9fafc" >预订酒店</span>
            <div class="reserve-input1" style="position: relative;min-height: 20px;left: 0" >
              <el-input
                  v-model="formhotel.hotelname"
                  placeholder="酒店名称"
                  style="width: 35%;"
              >
              </el-input>

              <div style="height: 70px">
                <el-date-picker
                    v-model="formhotel.timerange"
                    type="daterange"
                    start-placeholder="入住时间"
                    end-placeholder="退房时间"
                    :default-time="['00:00:00', '23:59:59']"
                    style="position: relative;left: 10%"
                >
                </el-date-picker>
              </div>
            </div>
            <div class="reserve-input2" style="position: relative;left:0%;min-height:0">
              <el-select v-model="formhotel.rating" placeholder="请选择" >
                <el-option
                    v-for="item in options"
                    :key="item.value"
                    :label="item.label"
                    :value="item.value"
                >
                  <span style="float: left">{{ item.label }}</span>
                  <span style="float: right; color: #8492a6; font-size: 13px">{{ item.value }}</span>
                </el-option>
              </el-select>
              <el-input
                  v-model="formhotel.keyword"
                  placeholder="关键词(选填)"
                  style="width:25% ;position: relative;left:3%"
              >
              </el-input>
            </div>
            <el-button  @click="search(formhotel)" type="primary" icon="el-icon-search" style="color: white;background-color: rgb(132,146,166);position: relative;left: 40%;">
              搜索
            </el-button>
          </div>
        </el-header>
        <el-main>
          <el-container class="Hotel-recommendation-container">
            <el-header class="third-header" style="display: flex">
              <div style="position: relative;left: 50%" >
                <span style="font-size:28px;line-height:1.3;position: relative;left:-40%;color:black" >酒店推荐</span>
              </div>
            </el-header>
            <el-main style="height: 300%; width: auto" >
              <div  class="hotel-list" style="display: flex; justify-content: space-between;position: relative;height: 100%;width: 70%; left: 10%;right: 10%">
                <div>
                  <el-carousel indicator-position="outside" style="position: relative;width:600%;height:100%;left: 170%;">
                    <el-carousel-item v-for="item in 4" :key="item" style="border-radius:36px">
                      <img src="../../static/hotel1.png" alt="" style="position: relative;width:100%;height:100%;border-radius:36px">
                    </el-carousel-item>
                  </el-carousel>
                </div>
              </div>
            </el-main>
          </el-container>
        </el-main>
      </el-main>
    </el-container>
  </div>
</template>

<script>
import axios from "axios";
import router from "@/router";
import HotelList from "@/pages/HotelList";


export default {
  name: "MainInterface",
  components: {HotelList},
  data(){
    return{
      options: [{
        value: '选项1',
        label: '五星'
      }, {
        value: '选项2',
        label: '四星'
      }, {
        value: '选项3',
        label: '三星及以下'
      }, ],
      formhotel: {
        hotelname: '',
        keyword: '',
        timerange: '',
        rating: ''
      }
    }
  },

  methods: {
    search(formhotel) {
      formhotel=this.formhotel
      axios.post('/back/hotel/queryhotelbysome',"hname="+this.formhotel.hotelname+"&keyword="+this.formhotel.keyword+"&rating="+this.formhotel.rating).then(function (response){
        console.log(formhotel.hotelname)
        console.log(response)
        var searchresult=response.data.result
        this.$cookies.set('searchresult',searchresult);
        if(response.status === 200)
        {
            router.push('/hotellist')
            console.log('下面是传到cookies的结果')
            console.log(this.$cookies.get(searchresult))
        }
      }).catch(failResponse => {}).catch(error => {
        console.log(error.response.data.code)
      })
    },

  }
}
</script>

<style   scoped>
.reserve-container{
  height: 100%;
}

.secend-header{
  background-color:#8492a6;
  color: #333;
  text-align: center;
  line-height: 60px;

}
.first-header{
  display: flex;
  background: white;
  justify-content: space-between;
  /*padding-left:25% ;*/
  align-items: center;
}

.reserve-input1{
  display: flex;
  /*justify-content: center;*/
}
.reserve-input2{
  display: flex;
  /*justify-content: center;*/
}

.el-carousel{
  border-radius: 36px;
}

</style>